const LocationService = () => {
  let subscriber = null
  let location = {
    latitude: 0,
    longitude: 0,
    accuracy: 0,
    speed: 0,
  }

  return {
    subscribe: (sub) => subscriber = sub,

    setLocation: (coords) => {
      location = coords
      typeof subscriber === 'function' && subscriber(location)
    },

    unsubscribe: (sub) => {
      subscriber = null
    }
  }
}

export const locationService = LocationService()
