import { log } from './config';
log.setSeverity('info')
log.debug('\n\n\nhumanity started\n\n\n');
//import('./ReactotronConfig').then(() => log.info('Reactotron: configured'))

import * as React from 'react';
import { View, Text, Button, StyleSheet, AsyncStorage, Linking, AppState,
  NativeModules, DeviceEventEmitter, NativeAppEventEmitter } from "react-native";
import { WebView } from "react-native-webview";
import * as TaskManager from 'expo-task-manager';
import * as Location from 'expo-location';
import * as Permissions from 'expo-permissions';
import ActivityRecognition from 'react-native-activity-recognition'
//import { NavigationContainer } from '@react-navigation/native';
//import { createStackNavigator } from '@react-navigation/stack';
//import Realm from 'realm';
import * as Progress from 'react-native-progress';
import hyperid from 'hyperid';
import BackgroundTimer from 'react-native-background-timer';

import { locationService } from './Services';
import Sensors from './Sensors';
import Weather from './Weather';
import Mood from './Mood';
import DebugView from './DebugView';
import { Spotify, AUTH_PAGE_URI, REDIRECT_URI } from './Spotify';
//import ThreeD from './ThreeD';

const { SensorManager, RNSoundLevelModule } = NativeModules

TaskManager.defineTask('background-location-check', async ({ data, error }) => {
  if (error) {
    log.error(error.message)
    return
  }

  if (data) {
    const { latitude, longitude, accuracy, speed } = data.locations[0].coords
    locationService.setLocation({ latitude, longitude, accuracy, speed })
  }
})

export default class App extends React.Component {

  state = {
    // App state related
    foreground: true,
    history: [],
    lastHistoryCompaction: null,
    isLoggedIn: false,
    sensorsNeedRefresh: true,
    sensorsRefreshing: false,
    loadingProgress: undefined,
    findingProgress: 100,
    requireManualLogin: false,
    tokenRefreshRequired: false,

    // Spotify related
    spotifyToken: null,
    lastPlayedTracks: [],
    savedTracks: [],
    topTracks: [],
    topTracksLongTerm: [],
    topArtists: [],
    topArtistsLongTerm: [],
    topGenres: [],
    topGenresLongTerm: [],
    lastCreatedPlaylists: [],
    spotifyTokenExpires: null,
    lastPlayedRefresh: null,
    savedTracksRefresh: null,
    topTracksRefresh: null,
    topArtistsRefresh: null,

    // Mood related
    currentMood: null,
    currentTuneableParams: null,

    // Sensors/data related
    latitude: null,
    longitude: null,
    accuracy: null,
    speed: null,
    isWiFiConnected: false, // If netInfo returns a strong wifi signal
    ambientLight: 0,  // 0 (dark) to ~32000 (light), depends a lot on phone's location
    isNear: false,    // Something close to the proximity detector (pocket, face)
    soundLevel: 0,    // ~ -220db to 0
    activity: null,   // Object with attr 'type':
    // [IN_VEHICLE, ON_BICYCLE, ON_FOOT, RUNNING, WALKING, STILL, TILTING, UNKNOWN]
    // and 'confidence' in percent

    sunset: null,
    sunrise: null,
    moonhigh: null,
    moonrise: null,
    moonset: null,
    moonphase: null, // 0 - 50: waxing, 50 - 100: wanning (0 new moon, 50 full moon)

    temperature: null, // Celsius
    pressure: null, // hPa
    humidity: null, // %
    cloudiness: null, // %
    fog: null, // %
    altitude: null, // meters
    windSpeed: null, // beaufort
    rain: null, // mm last hour

    lastSensorsCheck: null,
    lastWeatherCheck: null,
    lastAstronomyCheck: null,
  }

  backgroundTasks = async () => {
    const {
      latitude,
      longitude,
      spotifyToken,
      spotifyTokenExpires,
      sensorsNeedRefresh,
      lastSensorsCheck,
      lastWeatherCheck,
      lastAstronomyCheck,
      lastHistoryCompaction,
      savedTracksRefresh,
      topTracksRefresh,
      topArtistsRefresh,
    } = this.state

    const now = new Date()
    const inTenMin = new Date().setMinutes(new Date().getMinutes() + 10)
    const tenMinAgo = new Date().setMinutes(now.getMinutes() - 10)
    const oneMinAgo = new Date().setMinutes(now.getMinutes() - 1)
    const thirtyMinAgo = new Date().setMinutes(now.getMinutes() - 30)
    let thisHour = new Date(); thisHour.setSeconds(0); thisHour.setMinutes(0)
    let thisDay = new Date(); thisDay.setSeconds(0); thisDay.setMinutes(0); thisDay.setHours(0)

    const token = spotifyToken
    const tokenExpired = Boolean(!spotifyTokenExpires || new Date(spotifyTokenExpires) < inTenMin)

    const steps = [

    // 1. Spotify
    async () => {
      if (tokenExpired) {
        log.info('Token: needs refresh')

        return this.setStateAsync({
          tokenRefreshRequired: true,
          requireManualLogin: spotifyTokenExpires === null
        })
      } else return
    },

    // 2. Sensors
    async () => {
      if (!lastSensorsCheck || new Date(lastSensorsCheck) < thirtyMinAgo || sensorsNeedRefresh) {
        clearTimeout(this.refreshTimeout)
        this.refreshTimeout = setTimeout(async () => {
          log.info('Sensors: refreshing')
          this.setState({ sensorsRefreshing: true })
          const { coords } = await Sensors.getLocation()
          this.setState({ coords })
          Sensors.startTemporarily()
          Sensors.getNetInfo((isWiFiConnected) => this.setState({ isWiFiConnected }))
        }, 1500)

        BackgroundTimer.clearTimeout(this.storeTimeout)
        this.storeTimeout = BackgroundTimer.setTimeout(() => {
          const { activity, isNear, soundLevel, ambientLight, latitude, longitude,
            accuracy, speed, isWiFiConnected } = this.state
          this.setState({ sensorsNeedRefresh: false, sensorsRefreshing: false })
          this.storeState({ activity, isNear, soundLevel, ambientLight, latitude,
            longitude, accuracy, speed, isWiFiConnected, lastSensorsCheck: new Date() })
        }, 8000)
      }

      return
    },

    // 3. Weather
    async () => {
      if (latitude && (!lastWeatherCheck || new Date(lastWeatherCheck) < thisHour)) {
        log.info('Weather: fetching')
        this.setState({ loadingProgress: 10 })

        const weatherData = await Weather.getForecast({ latitude, longitude })
        return this.storeStateAsync({ ...weatherData, lastWeatherCheck: new Date()})
      } else return
    },

    // 4. Astronomy
    async () => {
      if (latitude && (!lastAstronomyCheck || new Date(lastAstronomyCheck) < thisDay)) {
        log.info('Astronomy: fetching')
        this.setState({ loadingProgress: 15 })

        const astroData = await Weather.getAstronomy({ latitude, longitude })
        await this.storeStateAsync({ ...astroData, lastAstronomyCheck: new Date()})
      } else return
    },

    // 5. Compact history
    async () => {
      if (!lastHistoryCompaction || new Date(lastHistoryCompaction) < thisDay) {
        log.info('History: compacting')
        this.setState({ loadingProgress: 20 })

        await new Promise(resolve => this.compactHistory(resolve))
        return this.storeStateAsync({ lastHistoryCompaction: new Date() })
      }
    },

    // 6. Get saved tracks features
    async () => {
      if (!tokenExpired && (!savedTracksRefresh || new Date(savedTracksRefresh) < thisDay)) {
        log.info('Spotify: refreshing saved tracks')
        this.setState({ loadingProgress: 25 })

        const allSavedTracks = await Spotify.getSavedTracks(token)
        const oldSavedTracks = this.state.savedTracks || []
        const newSavedTracks = allSavedTracks.map(newTrack => (
          oldSavedTracks.map(t => t.id).includes(newTrack.id) ?
            oldSavedTracks.find(t => t.id === newTrack.id) : newTrack
        ))
        await this.storeStateAsync({ savedTracks: newSavedTracks })
        this.setState({ loadingProgress: 30 })

        const savedTracks = await Spotify.getTrackFeatures(token, this.state.savedTracks)
        return this.storeStateAsync({ savedTracks, savedTracksRefresh: new Date() })
      } else return
    },

    // 7. Get top tracks features
    async () => {
      if (!tokenExpired && (!topTracksRefresh || new Date(topTracksRefresh) < thisDay)) {
        log.info('Spotify: refreshing top tracks')
        this.setState({ loadingProgress: 60 })

        const allTopTracksIDs = await Spotify.getTopTracks(token, {offset: 0, limit:50})
        const allTopTracks = allTopTracksIDs.map(id => ({ id, features: {}}))
        const oldTopTracks = this.state.topTracks || []
        const newTopTracks = allTopTracks.map(newTrack => (
          oldTopTracks.map(t => t.id).includes(newTrack.id) ?
            oldTopTracks.find(t => t.id === newTrack.id) : newTrack
        ))
        await this.storeStateAsync({ topTracks: newTopTracks })
        this.setState({ loadingProgress: 65 })

        const topTracks = await Spotify.getTrackFeatures(token, this.state.topTracks)
        return this.storeStateAsync({ topTracks, topTracksRefresh: new Date() })
      } else return
    },

    // 8. Refresh top artists TODO: remove stale
    async () => {
      if (!tokenExpired && (!topArtistsRefresh || new Date(topArtistsRefresh) < thisDay)) {
        log.info('Spotify: refreshing top artists')
        this.setState({ loadingProgress: 90 })

        const topArtists = await Spotify.getTopArtists(token, {})
        const topArtistsLongTerm = await Spotify.getTopArtists(token, { time_range: 'long_term' })
        return this.storeStateAsync({
          topArtists,
          topArtistsLongTerm,
          topArtistsRefresh: new Date()
        })
      } else return
    }]

    for (const step of steps) {
      typeof step === 'function' && await step()
    }

    this.setState({ loadingProgress: 100 })
  }

  async componentDidMount() {

    this.getLastKnownState(() => {
      this.setState({ loadingProgress: 5 })

      log.info('Adding listerners')
      this.lightListener = DeviceEventEmitter.addListener('LightSensor', this.onLightUpdate)
      this.proximityListener = DeviceEventEmitter.addListener('Proximity', this.onProximityUpdate)
      this.soundListener = NativeAppEventEmitter.addListener('frame', this.onSoundUpdate)

      if (AppState._eventHandlers.change.size === 0 && !this.appStateListener) {
        this.appStateListener = AppState.addEventListener("change", this.onAppStateChanged)
      }

      this.activityUnsubscribe = ActivityRecognition.subscribe(this.onActivityUpdate)

      locationService.subscribe(this.onLocationUpdate)

      Sensors.startLocationUpdates()

      this.backgroundTasks()
      this.launchBackgroundTasksNow()
    })
  }

  componentWillUnmount() {
    log.info('App: flushing timers')

    BackgroundTimer.clearInterval(this.backgroundInterval)

    if (this.appStateListener)
      this.appStateListener.remove()

    if (this.lightListener)
      this.lightListener.remove()

    if (this.proximityListener)
      this.proximityListener.remove()

    if (this.soundListener)
      this.soundListener.remove()

    this.activityUnsubscribe()

    locationService.unsubscribe(this.onLocationUpdate)

  }

  onAppStateChanged = (state) => {
    log.debug(`App state: ${state}`)
    const foreground = state === "active" && !this.state.foreground
    this.setState({ foreground })
  }

  onLocationUpdate = ({ latitude, longitude, accuracy, speed }) => {
    log.debug('location received')
    if (latitude !== this.state.latitude || longitude !== this.state.longitude) {
      this.setState({ latitude, longitude, accuracy, speed, sensorsNeedRefresh: true })
    }
  }

  onLightUpdate = (data) => {
    log.debug('light received')
    log.debug(data.light)
    this.setState({ ambientLight: data.light })
  }

  onProximityUpdate = (data) => {
    log.debug('proximity received')
    log.debug(data.isNear)
    this.setState({ isNear: data.isNear })
  }

  onSoundUpdate = (data) => {
    log.debug('sound received')
    log.debug(data.value)
    this.setState({ soundLevel: data.value })
  }

  onActivityUpdate = (data) => {
    log.debug('activity received')
    log.debug(data)

    const activity = data.sorted[0]
    if (activity !== this.state.activity) {
      this.setState({ activity })
    }
  }

  onWeatherUpdate = (data) => {
    log.debug('weather received')
    this.setState({ ...data })
  }

  setStateAsync(state) {
    return new Promise((resolve) => {
      this.setState(state, resolve)
    });
  }

  storeStateAsync(state) {
    return new Promise((resolve) => {
      this.storeState(state, resolve)
    });
  }

  launchBackgroundTasksNow = () => {
    this.backgroundInterval = BackgroundTimer.setInterval(() => {
      const { loadingProgress, sensorsRefreshing } = this.state
      if (!sensorsRefreshing && (loadingProgress < 10 || loadingProgress == 100)) {
        this.backgroundTasks()
      }
    }, 5000);
  }

  getLikedTracks = async () => {
    const token = this.state.spotifyToken
    const likedTracks = await Spotify.getSavedTracks(token)

    this.storeState({ likedTracks })
  }

  getTrackFeatures = async () => {
    const token = this.state.spotifyToken
    const likedTracks = this.state.likedTracks
    const analyzedTracks = await Spotify.getTrackFeatures(token, likedTracks)

    this.storeState({ likedTracks: analyzedTracks })
  }

  getTopArtists = async () => {
    const token = this.state.spotifyToken
    const topArtists = Spotify.getTopArtists(token)
    const topArtistsLongTerm = Spotify.getTopArtists(token, { time_range: 'long_term' })

    this.storeState({
      topArtists,
      topArtistsLongTerm,
      topArtistsRefresh: new Date()
    })
  }

  checkWeather = async (coords) => {
    log.info('Weather: checking')
    await this.fetching('weather')
    const weatherData = await Weather.getForecast(coords)
    await this.fetched('weather')

    return this.storeState({ ...weatherData, lastWeatherCheck: new Date()})
  }

  checkAstronomy = async (coords) => {
    log.info('Astronomy: checking')
    await this.fetching('astronomy')
    const astroData = await Weather.getAstronomy(coords)
    await this.fetched('astronomy')

    return this.storeState({ ...astroData, lastAstronomyCheck: new Date()})
  }

  debugState = () => {
    console.log(this.state)
  }

  storeTokenWhenAvailable = (event) => {
    const { url } = event

    if (url && url.startsWith(REDIRECT_URI)) {
      log.info('Token: intercepting auth request')
      const uri = url.replace(`${REDIRECT_URI}#`, '')
      let { access_token, expires_in } = Spotify.getTokenObjectFromURI(uri)
      let expireDate = new Date()
      let now = new Date()
      expireDate.setSeconds(now.getSeconds() + parseInt(expires_in))

      this.setState({
        isLoggedIn: true,
        requireManualLogin:false,
        tokenRefreshRequired: false,
      }, () => {
        this.storeState({
          spotifyToken: access_token,
          spotifyTokenExpires: expireDate
        })
      })

      return false // do not go on with loading
    }

    return true
  }

  ensureSpotifyConnected = () => {
    const { spotifyToken, spotifyTokenExpires } = this.state

    const expiresAt = new Date(spotifyTokenExpires)
    const inTenMin = new Date().setMinutes(new Date().getMinutes() + 10)

    if (expiresAt < inTenMin) {
      log.info('Token: needs refresh')
      this.setState({
        tokenRefreshRequired: true,
        requireManualLogin: spotifyTokenExpires === null
      })
    }
  }

  refreshMood = () => {
    const mood = Mood.evaluate(this.state)
    return this.setStateAsync({ mood })
  }
 
  refreshLastPlayedTracks = (token) => {
    log.info('Spotify: getting last played Tracks')
    const recentlyPlayedTracks = this.state.lastPlayedTracks
    const lastRefresh = this.state.lastPlayedRefresh

    return Spotify.getLastPlayedTracks(token, lastRefresh)
      .then(lastPlayedTracks =>
        this.storeStateAsync({
          lastPlayedTracks: [...recentlyPlayedTracks, ...lastPlayedTracks],
          lastPlayedRefresh: new Date()
        })
      )
  }

  refreshExplorationLevel = (action) => {
    log.info('App: refreshing exploration level')
    const { explorationLevel, explorationLevelRefresh } = this.state
    const now = new Date()
    const threeHoursAgo = new Date().setHours(now.getHours() - 3)
    let newExplorationLevel = action === 'expect' ? 0.30 : 0.70

    if (explorationLevelRefresh && new Date(explorationLevelRefresh) > threeHoursAgo) {
      const threeHoursAfter = new Date(explorationLevelRefresh)
        .setHours(new Date(explorationLevelRefresh).getHours() + 3)
      var timeDiff = Math.abs(new Date(explorationLevelRefresh) - now)
      var minDiff = Math.floor((timeDiff/1000)/60)
      var totalMin = 180
      var explorationDiff = explorationLevel - 0.50
      var actionDiff = action === 'expect' ? -0.20 : 0.20
      newExplorationLevel = 0.50 + (minDiff * explorationDiff / totalMin) + actionDiff
    }

    if (newExplorationLevel > 1) newExplorationLevel = 1
    if (newExplorationLevel < 0) newExplorationLevel = 0

    // toFixed 2 decimals
    newExplorationLevel = (+(Math.round(+(newExplorationLevel + 'e' + 2)) + 'e' + -2)).toFixed(2)

    return this.storeStateAsync({
      explorationLevel: newExplorationLevel,
      explorationLevelRefresh: now
    })
  }

  getSelection = async (action='expect') => {
    const token = this.state.spotifyToken

    this.setState({ findingProgress: 0 })
    await this.refreshMood()

    this.setState({ findingProgress: 5 })
    await this.refreshLastPlayedTracks(token)

    this.setState({ findingProgress: 15 })
    await this.refreshExplorationLevel(action)

    this.setState({ findingProgress: 20 })
    const tracks = await Spotify.selectTracks(
      token,
      this.state.savedTracks,
      this.state.topTracks,
      this.state.topArtists,
      this.state.lastPlayedTracks,
      this.state.explorationLevel,
      this.state.mood,
      action
    )

    this.setState({ findingProgress: 80 })
    const playlistID = await Spotify.createMoodPlaylist(token, this.state.mood)
    await this.storeStateAsync({
      lastCreatedPlaylists: [...this.state.lastCreatedPlaylists,
        { id: playlistID, created_at: new Date() }]
    })

    this.setState({ findingProgress: 90 })
    await Spotify.addTracksToPlaylist(token, playlistID, tracks)

    //if (await Spotify.isAnyActiveDevice(token)) {
    //  Spotify.playPlaylist(token, playlistID) // TODO: debug
    //} else {
    Spotify.openPlaylistLink(playlistID)
    this.setState({ findingProgress: 100 })
    //}
  }

  getRecommendedSavedTracks = async (action='explore') => {
    const tracks = this.state.topTracks
    //const tracks = this.state.savedTracks
    const mood = Mood.evaluate(this.state)

    const reco = Spotify.getRecommendedFavTracks(tracks, mood)
  }

  uuid = hyperid({fixedLength: true})

  storeState = (changes, callback) => {
    this.setState(changes, () => {
      this.storeFrame(changes, () =>
        callback && callback()
      )
    })
  }

  storeFrame = (changes, callback, ts) => {

    const id = this.uuid()
    const timestamp = Math
      .floor(Date.parse(new Date()) / 1000)
      .toString()
    const frame = {
      timestamp: ts ? ts: timestamp,
      changes: changes
    }

    log.debug('Storing: ')
    log.debug(changes)
    AsyncStorage.setItem(id, JSON.stringify(frame), () => { callback() })
  }

  getHistory = (callback) => {
    log.info('History: fetching from storage')
    AsyncStorage.getAllKeys((err, keys) => {
      const frameList = keys.filter(key => key.length === 33)
      const toObject = (frame) => {
        const uid = frame[0]
        const { timestamp, changes } = JSON.parse(frame[1])

        // Revive Date objects...
        const datedChanges = Object.keys(changes).includes('sunrise') ?
          {...changes,
            moonhigh: new Date(changes.moonhigh),
            moonrise: new Date(changes.moonrise),
            moonset: new Date(changes.moonset),
            sunrise: new Date(changes.sunrise),
            sunset: new Date(changes.sunset)} : changes

        return { timestamp, changes: datedChanges, uid }
      }

      const orderChronologically = (f1, f2) => {
        return parseInt(f1.timestamp) - parseInt(f2.timestamp)
      }

      const getFrames = (frames, frame) => {
        return [...frames, frame ]
      }

      const getTimeline = (timestamps, { timestamp }) => {
        return [...timestamps, timestamp ]
      }

      AsyncStorage.multiGet(frameList, (err, rawFrames) => {
        const frames = rawFrames
          .map(toObject)
          .sort(orderChronologically)

        const timeline = frames
          .reduce(getTimeline, [])

        callback({ frames, timeline })
      })
    })
  }

  getLastKnownState = (callback) => {
    log.info('History: getting last known state')

    this.getHistory(({ frames, timeline }) => {
      const getLastState = (state, { changes }) => {
        return { ...state, ...changes }
      }

      const lastState = frames
        .reduce(getLastState, {...this.state})

      this.setState({ ...lastState }, () => callback && callback())
    })

  }

  compactHistory = (callback) => {

      const now = new Date()
      const oneDayAgo = new Date().setHours(now.getHours() - 24)
      const timestamp = Math.floor(1+oneDayAgo / 1000).toString()

      this.getHistory(({ frames }) => {

        const oldFrames = frames.filter(frame =>
          new Date(parseInt(frame.timestamp) * 1000) < oneDayAgo
        )

        const oneDayAgoState = oldFrames.reduce((state, { changes }) => {
          return { ...state, ...changes }
        }, {})

        const keysToRemove = [...oldFrames.map(frame => frame.uid),
        ...frames.filter(f => f.timestamp === "NaN").map(f => f.uid)]

        if (keysToRemove.length > 0) {
          AsyncStorage.multiRemove(keysToRemove, (err) => {
            this.storeFrame(oneDayAgoState, () => {
              callback && callback()
            }, timestamp)
          })
        } else {
          callback && callback()
        }
      })

  }

  debugSomething = () => {
    console.log(this.state.ambientLight)
    console.log(this.state.soundLevel)
    console.log(this.state.accuracy)
    console.log(this.state.speed)
    console.log(this.state.isWiFiConnected)
  }

  render() {
    const {
      requireManualLogin,
      tokenRefreshRequired,
      loadingProgress,
      findingProgress,
      sensorsRefreshing,
    } = this.state

    const LoginPage = <WebView
      source={{uri: AUTH_PAGE_URI}}
      style={{marginTop: 0}}
      onShouldStartLoadWithRequest={(e) => this.storeTokenWhenAvailable(e)}
      onError={(e) => {}}
    />
    //  style={{marginTop: 0, height: hidden ? 0 : 'inherit'}}


    if (requireManualLogin) {
      return LoginPage
    } else if (loadingProgress !== 100 || sensorsRefreshing || findingProgress !==100) {
      return <View style={styles.container}>
        { tokenRefreshRequired && LoginPage }
        { (loadingProgress === undefined || sensorsRefreshing) && <Progress.Bar
        indeterminate={true}
        /> }
        { !sensorsRefreshing && loadingProgress >= 0 && <Progress.Bar
          indeterminate={false}
          progress={loadingProgress/100}
        /> }
        { findingProgress >= 0 && findingProgress !== 100 && <Progress.Bar
          indeterminate={false}
          progress={findingProgress/100}
        /> }
      </View>
    } else {
      return <View style={styles.container}>
        { tokenRefreshRequired && LoginPage }
         <DebugView
          rawSensorData={this.state}
          mood={() => Mood.evaluate(this.state)}
          selectNewSongs={(action) => this.selectNewSongs(action)}
          getSelection={(action) => this.getSelection(action)}
          debugState={() => this.debugState()}
          getExperience={() => this.getExperience()}
          getLikedTracks={() => this.getLikedTracks()}
          getRecommendedSavedTracks={() => this.getRecommendedSavedTracks()}
          debugSomething={() => this.debugSomething()}
        />
      </View>
    }
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  bar: {
    height: 80
  },
});
