import { log } from './config';
import React, { Component } from 'react';
import { Linking } from 'react-native';
import { WebView } from "react-native-webview";
import RnHash, { CONSTANTS } from "react-native-hash";
import Mood from './Mood';

// App TODO: hide this until production
const SPOTIFY_CLIENT_ID = '87ced00c9b9640b9858f9cdf1fa38832'

const SPOTIFY_SCOPES = [
  'playlist-read-collaborative',
  'playlist-read-private',
  'playlist-modify-private',
  'user-read-private',
  'user-read-playback-position',
  'user-read-playback-state',
  'user-read-currently-playing',
  'user-read-recently-played',
  'user-library-read',
  'user-top-read',
  'user-modify-playback-state',
]

const SPOTIFY_OPEN_ENDPOINT = "https://open.spotify.com"
const SPOTIFY_API = "https://api.spotify.com/v1"

function paramsToURI(params) {
  return Object.keys(params).map((key) => {
    return encodeURIComponent(key) + '=' + encodeURIComponent(params[key]);
  }).join('&');
}

function formatAMPM(date) {
  var hours = date.getHours();
  var minutes = date.getMinutes();
  var ampm = hours >= 12 ? 'PM' : 'AM';
  hours = hours % 12;
  hours = hours ? hours : 12; // the hour '0' should be '12'
  minutes = minutes < 10 ? '0'+minutes : minutes;
  var strTime = hours + ':' + minutes + ampm;
  return strTime;
}

function sample(sampleSize, array, preSample=[], exclude=[], favorFirsts=false) {
  let sample = [...preSample]
  let candidate = undefined
  const maxAttempts = 50
  let attempts = 0
  let triedIndexes = []

  while(sample.length < sampleSize + preSample.length && attempts <= maxAttempts
    && triedIndexes.length < array.length) {
    const random1 = Math.round(Math.random() * array.length)
    const random2 = Math.round(Math.random() * random1)
    const index = favorFirsts ? random2 : random1

    if (triedIndexes.includes(index)) {
      continue
    } else {
      triedIndexes.push(index)
    }

    candidate = array[index]

    if (typeof candidate === "object"
    && Object.keys(candidate).includes('id')
    && sample.map(v => v.id).includes(candidate.id)
    || exclude.length > 0
    && Object.keys(exclude[0]).includes('id')
    && exclude.map(v => v.id).includes(candidate.id)
    || sample.includes(candidate)
    || exclude.includes(candidate)) {
      attempts++
      continue
    }

    sample.push(candidate)
  }

  if (attempts >= maxAttempts) {
    log.warn('Sample: overlooping')
  }

  if (sample.length < sampleSize) {
    log.info(`Sample: could only select ${sample.length} out of ${sampleSize} tracks`)
  }

  return sample.filter(v => v !== undefined)
}

function shuffle(array) {
  var currentIndex = array.length, temporaryValue, randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {

    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array;
}

export const parseURI = function() {
}

export const REDIRECT_URI = 'http://localhost:5745/'

const URILoginParams = paramsToURI({
  client_id: SPOTIFY_CLIENT_ID,
  response_type: 'token',
  redirect_uri: REDIRECT_URI,
  scope: SPOTIFY_SCOPES,
})

export const AUTH_PAGE_URI = `https://accounts.spotify.com/authorize?${URILoginParams}`

export const Spotify = {

  getTokenObjectFromURI(uri) {
    let result = {}

    var vars = uri.split('&');
    for (var i = 0; i < vars.length; i++) {
      var pair = vars[i].split('=');
      if (decodeURIComponent(pair[0]) === 'access_token') {
        result.access_token = decodeURIComponent(pair[1]);
      } else if (decodeURIComponent(pair[0]) === 'expires_in') {
        result.expires_in = decodeURIComponent(pair[1]);
      }
    }

    return result
  },

  openTrackLink(trackID) {
    // TODO: add bundle ID ?utm_campaign=com.app
    const link = `${SPOTIFY_OPEN_ENDPOINT}/track/${trackID}`
    log.info(`Spotify: opening ${link}`)
    Linking.openURL(link)
  },

  openPlaylistLink(playlistID) {
    // TODO: add bundle ID ?utm_campaign=com.app
    const link = `${SPOTIFY_OPEN_ENDPOINT}/playlist/${playlistID}`
    log.info(`Spotify: opening ${link}`)
    Linking.openURL(link)
  },

  API(token, { uri, params }) {
    const headers = new Headers({ "Authorization": `Bearer ${token}` })
    const url = `${SPOTIFY_API}${uri}`

    log.info(`API: fetching ${url}`)
    log.info(`with params ${params}`)
    log.debug(`and token ${token}`)

    return fetch(url, { headers: headers, ...params })
      .then(result => result.json())
  },

  async isAnyActiveDevice(token) {
    const res = await this.API(token, { uri: `/me/player/devices` })
    return res.devices.length > 0 && res.devices[0].is_active
  },

  getTopTracks(token, {offset='random', limit=5, time_range='medium_term'}) {
    log.info(`Spotify: getting top tracks (offset=${offset}, limit=${limit}, time_range=${time_range})`)

    // This way we get more Top tracks than 100-from-the-top tracks
    const chosenOffset = offset === 'random' ?
      Math.round(Math.random() * 100): offset

    const params = { limit, time_range, offset: chosenOffset }
    const uri = `/me/top/tracks?${paramsToURI(params)}`

    return this.API(token, { uri }).then((results) => {
      return results.items.map(track => track.id)
    })

  },

  getTopArtists(token, {limit=50, time_range='medium_term'}) {
    log.info(`Spotify: getting top artists (limit=${limit}, time_range=${time_range})`)

    const params = { limit, time_range }
    const uri = `/me/top/artists?${paramsToURI(params)}`

    return this.API(token, { uri }).then((results) => {
      return results.items.map(artist => artist.id)
    })

  },

  async getRecommendedTracks(token, params, options) {
    log.info(`Spotify: getting recommended tracks from:`)
    log.info(params)
    log.debug(`with options:`)
    log.debug(options)

    const { limit, offset, tuneable } = options
    const chosenOffset = offset === 'random' ?
      Math.round(Math.random() * 5) * Math.round(Math.random() * 10) : offset
    const newLimit = chosenOffset + limit

    const updatedParams = {
      ...params,
      ...tuneable,
      market: 'from_token',
      limit: newLimit
    }
    const uri = `/recommendations?${paramsToURI(updatedParams)}`

    return this.API(token, { uri }).then((results) => {
      return results.tracks === undefined ? [] :
        results.tracks.reduce((list, track, key) => {
          if (key >= chosenOffset) {
            return [...list, track.id]
          } else {
            return list
          }
        }, [])
    })

  },

  getLastPlayedTracks(token, after) {
    const params = { limit: 50 }

    if (after) {
      let date = new Date(after)
      params.after = date.setSeconds(0)
    }

    const uri = `/me/player/recently-played?${paramsToURI(params)}`

    return this.API(token, { uri }).then((results) => {
      return results.items === undefined ? [] :
        results.items.map(item => ({ id: item.track.id, played_at: item.played_at }))
    })

  },

  async getSavedTracks(token) {
    let needsToFetchMore = true
    let offset = 0
    let tracks = []
    let params = {
      limit: 50, // 50 by 50 maximum
      market: 'from_token'
    }

    while (needsToFetchMore) {
      needsToFetchMore = false
      params.offset = offset
      const uri = `/me/tracks?${paramsToURI(params)}`

      await this.API(token, { uri }).then((results) => {
        if (results.items && results.items.length > 0) {
          tracks = [...tracks, ...results.items.map(item => {
            return { id: item.track.id, features: {}}
          })]
          offset = offset + params.limit
          needsToFetchMore = results.items.length === 50
        }
      })
    }

    return tracks

  },

  async getTrackFeatures(token, tracks) {
    const featureList = ['key', 'mode', 'time_signature', 'acousticness',
      'danceability', 'energy', 'instrumentalness', 'liveness', 'loudness',
      'speechiness', 'valence', 'tempo']

    let analyzedTracks = []

    // Do it one by one instead of inside a Promise.all(map) to respect
    // Spotify rate limit
    for (const track of tracks) {
      // Check if this track's features have been fetched yet
      if (track.features.energy === undefined) {

        const uri = `/audio-features/${track.id}`
        const features = await this.API(token, { uri })

        let newFeatures = {}
        featureList.map(f => newFeatures[f] = features[f])

        analyzedTracks.push({ id: track.id, features: newFeatures })
      } else {
        analyzedTracks.push(track)
      }
    }

    return analyzedTracks
  },

  getRecommendedFavTracks(tracks, params) {
    log.info(`Spotify: get recommendations from favorite tracks`)

    return tracks.filter(track => {
      let meetsRequirements = true
      for (const param of Object.keys(params).filter(k => !k.startsWith('target_'))) {
        const paramValue = params[param]
        const paramKey = param.substr(4)
        const trackValue = parseFloat(track.features[paramKey])

        if (param.startsWith('max_') && trackValue > paramValue ||
        param.startsWith('min_') && trackValue < paramValue) {
          meetsRequirements = false
          break
        }
      }

      return meetsRequirements

    }).sort((trackA, trackB) => {
      let valueADifference = 0
      let valueBDifference = 0

      Object.keys(params)
        .filter(k => k.startsWith('target_'))
        .forEach(param => {
          const paramValue = params[param]
          const paramKey = param.substr(7)
          const trackAValue = parseFloat(trackA.features[paramKey])
          const trackBValue = parseFloat(trackB.features[paramKey])

          valueADifference += Math.abs(paramValue - trackAValue)
          valueBDifference += Math.abs(paramValue - trackBValue)
        })

      return valueADifference > valueBDifference
    }).map(t => t.id)
  },


  async selectTracks(token, savedTracks, topTracks, topArtists, lastPlayed,
    explorationLevel, mood, action='explore') {
    // If 'explore', highup the recommendation score for more discovery
    // If 'expect', lower it
    // Eventually, if no action is taken, the recommendation score settles
    //
    log.info(`Spotify: selecting track (${action})`)

    const lastPlayedTracks = lastPlayed.map(t => t.id)
    const playlistSize = 20
    const newTracksAmount = Math.floor(playlistSize * explorationLevel)
    const topTracksAmount = Math.floor((playlistSize - newTracksAmount) / 2)
    const savedTracksAmount = playlistSize - newTracksAmount - topTracksAmount

    log.info(`Selecting ${newTracksAmount} new tracks, \
${topTracksAmount} top tracks and ${savedTracksAmount} saved tracks`)

    const tuneable = Mood.getTuneable({...mood, action})
    const recommendedFavTracks = this.getRecommendedFavTracks(topTracks, tuneable)
    
    let selectedTracks = sample(
      topTracksAmount,
      recommendedFavTracks,
      [],
      lastPlayedTracks,
      true
    )

    selectedTracks = sample(
      savedTracksAmount,
      this.getRecommendedFavTracks(savedTracks, tuneable),
      selectedTracks,
      lastPlayedTracks,
      true
    )

    log.info(`Found ${selectedTracks.length} new tracks`)

    let spotifyRecommendedTracks = []
    let attempts = 0
    let seed_tracks= []
    let params = {}

    while (spotifyRecommendedTracks.length < newTracksAmount && attempts <= 4) {

      seed_tracks = sample(5, selectedTracks)
      if (seed_tracks.length < 4) {
        seed_tracks = sample(5, recommendedFavTracks)
      }

      if (seed_tracks.length < 4) {
        log.info('Falling back to artist recommendations')
        params.seed_artists = sample(5, topArtists)
      } else {
        params.seed_tracks = seed_tracks
      }

      const tracks = await this.getRecommendedTracks(token, params, {
        'offset': 0, // can be 'random'
        'limit': newTracksAmount + 3,
        'tuneable': tuneable,
      })

      const uniqueTracks = tracks.filter(id => {
        return !spotifyRecommendedTracks.includes(id) &&
          !selectedTracks.includes(id) && !lastPlayedTracks.includes(id)
      })

      log.info(`Found ${uniqueTracks.length} new tracks`)

      spotifyRecommendedTracks = spotifyRecommendedTracks.concat(uniqueTracks)

      attempts++
    }

    if (attempts === 4 && spotifyRecommendedTracks.length < newTracksAmount) {
      log.warn('Not enough new tracks found... Check your tuneable criterias')
    }

    selectedTracks = sample(
      newTracksAmount,
      spotifyRecommendedTracks,
      selectedTracks,
      lastPlayedTracks,
      false
    )

    shuffle(selectedTracks)

    return selectedTracks
  },

  queueTracks(token, tracks) {
    log.info(`Spotify: queueing tracks ${tracks}`)

    const headers = new Headers({
      "Authorization": `Bearer ${token}`,
      "Accept": "application/json"
    })

    const apiCalls = tracks.map(trackID => {
      return fetch(`${SPOTIFY_API}/me/player/queue?uri=spotify%3Atrack%3A${trackID}`, {
        method: 'POST',
        headers: headers,
      })
    })

    return Promise.all(apiCalls)
  },

  playTracks(token, tracks) {
    log.info(`Spotify: playing tracks ${tracks}`)

    const headers = new Headers({
      "Authorization": `Bearer ${token}`,
      "Accept": "application/json"
    })

    const trackURIS = tracks.map(id => `spotify:track:${id}`)

    return fetch(`${SPOTIFY_API}/me/player/play`, {
      method: 'PUT',
      headers: headers,
      body: JSON.stringify({
        uris: trackURIS,
      })
    })

  },

  playPlaylist(token, playlistID) {
    log.info(`Spotify: playing playlist ${playlistID}`)

    const headers = new Headers({
      "Authorization": `Bearer ${token}`,
      "Accept": "application/json"
    })

    const playlistURI = `spotify:playlist:${playlistID}`

    return fetch(`${SPOTIFY_API}/me/player/play`, {
      method: 'PUT',
      headers: headers,
      body: JSON.stringify({
        uris: playlistURI,
      })
    })

  },

  isCurrentlyPlaying(token) {
    log.info(`Spotify: checking if anything is currently playing`)

    const headers = new Headers({
      "Authorization": `Bearer ${token}`,
    })

    return new Promise(resolve => {
      fetch(`${SPOTIFY_API}/me/player/currently-playing`, { headers })
        .then(res => {
          if (res.status === 204) resolve(false)
          else return res.json()
        }).then(data => {
          if (data.is_playing) log.debug(`Spotify: currently playing`)
          return resolve(data.is_playing)
        })
    })
  },


  async createMoodPlaylist(token, mood) {
    log.info(`Spotify: creating a playlist for the hour:`)
    const moodHash = await RnHash.hashString(
      JSON.stringify(mood),
      CONSTANTS.HashAlgorithms.sha256
    )

    const headers = new Headers({
      "Authorization": `Bearer ${token}`,
      "Accept": "application/json",
      "Content-Type": "application/json"
    })

    const now = new Date()
    const today = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday',
      'Saturday'][now.getDay()]
    const time = formatAMPM(now)
    const name = `humanity - ${today} ${time}`

    log.info(name)

    const params = {
      name,
      public: false,
      collaborative: false,
      description: `${moodHash}`,
    }

    const user = await this.API(token, { uri: '/me' })
    const playlistID = await fetch(
      `${SPOTIFY_API}/users/${user.id}/playlists`,
      { method: 'POST', headers: headers, body: JSON.stringify(params) }
    ).then(res => res.json())
     .then(playlist => playlist.id)

    return playlistID
  },

  addTracksToPlaylist(token, playlistID, tracks) {
    log.info(`Spotify: adding tracks to the playlist ${playlistID}`)

    const headers = new Headers({
      "Authorization": `Bearer ${token}`,
      "Accept": "application/json",
      "Content-Type": "application/json"
    })

    const params = {
      uris: tracks.map(track => `spotify:track:${track}`)
    }
    const uri = `/playlists/${playlistID}/tracks`

    return fetch(`${SPOTIFY_API}${uri}`,
      {
        method: 'POST',
        headers: headers,
        body: JSON.stringify(params)
      }
    )
  },




}
