import { logger } from 'react-native-logs';

const colors = [
  console.log,   // debug
  console.log,   // info
  console.warn,  // warn
  console.error, // error
]

const colorConsole = (msg, level) => {
    /**
     * Control msg type
     * Here we use JSON.stringify so you can pass object, array, string, ecc...
     */
    let stringMsg;
    if (typeof msg === 'string') {
        stringMsg = msg;
    }
    else if (typeof msg === 'function') {
        stringMsg = '[function]';
    }
    else {
        stringMsg = msg === undefined ? undefined : JSON.stringify(msg);
      //stringMsg = msg;
    }
    let now = new Date()
    let output = `${now.toLocaleTimeString()} | ${level.text.toUpperCase()} | --- ${stringMsg}`;
    colors[level.severity](output || '');
    return true;
};

const log = logger.createLogger({
  transport: (msg, level) => {
    colorConsole(msg, level);
    return true;
  },
});

if (__DEV__) {
  log.setSeverity('debug');
} else {
  log.setSeverity('info'); //TODO: set to error in production
}

export { log };
