import React, { Component } from 'react'

const WEATHER_API_ENDPOINT = 'https://api.met.no/weatherapi/locationforecast/1.9/.json'
const ASTRONOMY_API_ENDPOINT = 'https://api.met.no/weatherapi/sunrise/2.0/.json'

const Weather = {

  // TODO: check expire headers
  getAstronomy({ latitude, longitude }) {
    const now = new Date()
    const date = `${now.getFullYear()}-${ ("0" + (now.getMonth() + 1)).slice(-2) }-${
      ("0" + now.getDate()).slice(-2) }`

    const convertMinsToHrsMins = function (minutes) {
      var h = Math.floor(minutes / 60);
      var m = minutes % 60;
      h = h < 10 ? '0' + h : h;
      m = m < 10 ? '0' + m : m;
      return h + ':' + m;
    }

    const offsetMin = now.getTimezoneOffset().toString()
    const absOffsetMin = offsetMin.includes('-') ? offsetMin.substring(1): offsetMin
    const offset = (offsetMin.includes('-') ? '+' : '-') + convertMinsToHrsMins(absOffsetMin)

    return fetch(`${ASTRONOMY_API_ENDPOINT}?lat=${latitude}&lon=${longitude}&date=${date}&offset=${offset}`)
      .then((response) => response.json())
      .then((astronomy) => {
        const astro = astronomy.location.time[0]
        return {
          sunset: new Date(astro.sunset.time),
          sunrise: new Date(astro.sunrise.time),
          moonset: new Date(astro.moonset.time),
          moonhigh: new Date(astro.high_moon.time),
          moonrise: new Date(astro.moonrise.time),
          moonphase: astro.moonphase.value
        }
      })

  },

  // TODO: check expire headers
  getForecast({ latitude, longitude }) {
    return fetch(`${WEATHER_API_ENDPOINT}?lat=${latitude}&lon=${longitude}`)
      .then((response) => response.json())
      .then((weather) => {
        const w = weather.product.time[0].location
        const r = weather.product.time[1].location

        return {
          temperature: parseInt(w.temperature.value),
          pressure: parseInt(w.pressure.value),
          humidity: parseInt(w.humidity.value),
          cloudiness: parseInt(w.cloudiness.percent),
          fog: parseInt(w.fog.percent),
          altitude: parseInt(w.altitude),
          windSpeed: parseInt(w.windSpeed.beaufort),
          rain: parseInt(r.precipitation.value),
        }
      })
  },

}

export default Weather;
