import { log } from './config';
import React from "react";
import { Pedometer } from "expo-sensors";
import { StyleSheet, Text, View, Button, Linking, AsyncStorage } from "react-native";

import { Spotify } from './Spotify';
import Mood from './Mood';

const styles = StyleSheet.create({
  container: {
    flex: 1,
    marginTop: 15,
    alignItems: "center",
    justifyContent: "center"
  }
});

export default class DebugView extends React.Component {
  state = {
    mood: null,
    tuneable: null
  };

  componentDidMount() {
    //this._subscribe();
  }

  componentWillUnmount() {
    //this._unsubscribe();
  }

  getMood = () => {
    this.setState({ mood: this.props.mood() })
  }

  getTuneable = () => {
    this.setState({ tuneable: Mood.getTuneable(this.state.mood) })
  }

  playTrack = (trackID) => {
    log.debug('Playing track', trackID)
    Spotify.openLink(trackID)
  }

  render() {
    const { location, isWalking, errorMsg, ambientLight, isNear, soundLevel, activity,
    sunset, sunrise, moonrise, moonset, moonphase, temperature, pressure, humidity,
     cloudiness, fog, altitude, windSpeed, rain, chosenTracks, latitude, longitude, accuracy }
    = this.props.rawSensorData

    const { mood, tuneable } = this.state

    const { navigation, selectNewSongs, getSelection } = this.props

    return <View style={styles.container}>
      { errorMsg && <Text> {errorMsg}</Text>}

      { activity && <Text>{`Your activity is: ${activity.type} (${activity.confidence}% certain)`}</Text> }
      { isNear && <Text>Your phone is near something</Text> }

      { [sunset, sunrise, moonrise, moonset, moonphase, temperature, pressure, humidity,
        cloudiness, fog, altitude, windSpeed, rain].map((obj, key) => {
          if (obj) return <Text key={key}>{key} : { obj.toString()}</Text>
        })
      }

      { mood && <Text>{JSON.stringify(mood)}</Text> }

      { tuneable && <Text>{JSON.stringify(tuneable)}</Text> }

      <Button
        onPress={() => this.getMood()}
        title="Mood"
      />

      <Button
        onPress={() => this.getTuneable()}
        title="Tuneable"
      />

      <Button
        onPress={() => getSelection('explore')}
        title='Explore'
      />

      <Button
        onPress={() => getSelection('expect')}
        title='Expect'
      />

      <Button
        onPress={() => getSelection('work')}
        title='Work'
      />

      <Button
        onPress={() => this.playTrack(chosenTracks[0])}
        title='Play track 1'
        disabled={!chosenTracks}
      />

      <Button
        onPress={() => { this.props.debugState() }}
        title='Debug state'
      />

      <Button
        onPress={() => { this.props.getExperience() }}
        title='Get Experience'
      />

      <Button
        onPress={() => { this.props.getLikedTracks() }}
        title='Get Liked Tracks'
      />

      <Button
        onPress={() => { this.props.debugSomething() }}
        title='Debug something'
      />

      <Button
        onPress={() => { this.props.getRecommendedSavedTracks() }}
        title='Get Recommended Saved Tracks'
      />

      <Button
        onPress={() => { log.warn('CLEARING HISTORY'); AsyncStorage.clear()}}
        title='Clear History'
      />


    </View>
  }
}

