import { log } from './config';

const Mood = {

  // Returns a mood Object {
  //   time: night time|early|morning|noon|afternoon|evening
  //   daytime: night|dawn|day|dusk
  //   temperature: siberia|freezing|cold|chill|cool|amazing|nice|warm|hot|burning|sahara
  //   wind: calm|breezy|windy|stormy
  //   weather: sunny|clear|partly sunny|cloudy|humid|hazy|rainy|gloomy|dark
  //   outdoor: yes|probably|probably not|no
  //   moon: invisible|visible|full
  //   ambiance: unknown|quiet|moderately loud|loud
  //   luminosity: unknown|dark|low|soft|moderate|luminous|radiant
  //   activity: unknown|still|walking|running|cycling|in vehicle
  // }
  //
  evaluate(state) {
    log.info('Mood: evaluating')

    let mood = {}
    const now = new Date()
    const { accuracy, speed, isWalking, errorMsg, ambientLight, isNear, soundLevel, activity,
    sunset, sunrise, moonhigh, moonset, moonphase, temperature, pressure, humidity,
    cloudiness, fog, altitude, windSpeed, rain, isWiFiConnected } = state

    const hours = now.getHours()
    if (hours >= 0 && hours < 5 || hours >= 22) {
      mood.time = 'night time'
    } else if (hours >= 5 && hours < 8) {
      mood.time = 'early'
    } else if (hours >= 8 && hours < 11) {
      mood.time = 'morning'
    } else if (hours >= 11 && hours < 14) {
      mood.time = 'noon'
    } else if (hours >= 14 && hours < 18) {
      mood.time = 'afternoon'
    } else if (hours >= 18 && hours < 22) {
      mood.time = 'evening'
    }

    let inOneHour = new Date()
    let oneHourAgo = new Date()
    inOneHour.setHours(hours + 1)
    oneHourAgo.setHours(hours - 1)

    if (now < sunrise && inOneHour < sunrise) {
      mood.daytime = 'night'
    } else if (now < sunrise && inOneHour >= sunrise) {
      mood.daytime = 'dawn'
    } else if (oneHourAgo >= sunrise && inOneHour < sunset) {
      mood.daytime = 'day'
    } else if (inOneHour >= sunset && now < sunset) {
      mood.daytime = 'dusk'
    } else if (oneHourAgo >= sunset) {
      mood.daytime = 'night'
    }

    const humidOrWindy = humidity > 65 || windSpeed > 3
    const humid = humidity > 75
    const windy = windSpeed > 4

    // Temperature scale, highly political, highly subjective
    // (from a Swede)
    //
    if (temperature >= 21 && temperature < 29) {
      mood.temperature = 'amazing'
    } else if (temperature >= 15 && temperature < 21) {
      if (humidOrWindy) {
        mood.temperature = 'chill'
      } else {
        mood.temperature = 'cool'
      }
    } else if (temperature >= 6 && temperature < 15) {
      if (humidOrWindy) {
        mood.temperature = 'cold'
      } else {
        mood.temperature = 'chill'
      }
    } else if (temperature >= -8 && temperature < 6) {
      if (humidOrWindy) {
        mood.temperature = 'freezing'
      } else {
        mood.temperature = 'cold'
      }
    } else if (temperature < -8) {
      if (humidOrWindy) {
        mood.temperature = 'siberia'
      } else {
        mood.temperature = 'freezing'
      }
    } else if (temperature >= 29 && temperature < 35) {
      if (humid) {
        mood.temperature = 'hot'
      } else if (windy) {
        mood.temperature = 'nice'
      } else {
        mood.temperature = 'warm'
      }
    } else if (temperature >= 35 && temperature < 41) {
      if (humid) {
        mood.temperature = 'burning'
      } else if (windy) {
        mood.temperature = 'warm'
      } else {
        mood.temperature = 'hot'
      }
    } else if (temperature >= 41) {
      if (windy) {
        mood.temperature = 'burning'
      } else {
        mood.temperature = 'sahara'
      }
    }

    // Weather conditions
    if (windSpeed <= 1) {
      mood.wind = 'calm'
    } else if (windSpeed <= 4) {
      mood.wind = 'breezy'
    } else if (windSpeed <= 10) {
      mood.wind = 'windy'
    } else if (windSpeed > 10) {
      mood.wind = 'stormy'
    }

    if (mood.day === 'night' || fog > 95 || cloudiness > 95) {
      mood.weather = 'dark'
    } else if (rain > 5) {
      mood.weather = 'rainy'
    } else if (fog > 35) {
      mood.weather = cloudiness > 50 ? 'gloomy': 'hazy'
    } else if (cloudiness > 80) {
      mood.weather = 'gloomy'
    } else if (rain > 0.5 && humidity > 75) {
      mood.weather = 'humid'
    } else if (cloudiness > 60) {
      mood.weather = 'cloudy'
    } else if (cloudiness > 20) {
      mood.weather = 'partly sunny'
    } else {
      mood.weather = mood.daytime !== 'day' ? 'clear': 'sunny'
    }

    // Try to deduce indoor/outdoor
    // Based on whether connected to a WiFi network and the GPS accuracy
    // Luminosity and ambient sound
    // TODO: check the time of the day?

    let outdoorScore = 0 // Positive: outdoor, negative: indoor

    // Indoor features
    if (isWiFiConnected) {
      outdoorScore -= 30
    }

    if (soundLevel < -170) {
      outdoorScore -= 30
    }

    if (accuracy > 1000) {
      outdoorScore -= 30
    }

    //  Outdoor features
    if (accuracy < 20) {
      outdoorScore += 30
    }

    if (ambientLight > 16000) {
      outdoorScore += 40
    } else if (ambientLight > 10000) {
      outdoorScore += 30
    } else if (ambientLight > 5000) {
      outdoorScore += 10
    }

    if (speed > 1) {
      outdoorScore += 30
    }

    if (outdoorScore > 0 && outdoorScore < 30) {
      mood.outdoor = 'probably'
    } else if (outdoorScore >= 30) {
      mood.outdoor = 'yes'
    } else if (outdoorScore <= 0 && outdoorScore > -30) {
      mood.outdoor = 'probably not'
    } else {
      mood.outdoor = 'no'
    }


    // Try to deduce if the moon is out
    // TODO: During the day the moon becomes invisible a bit before moonset
    mood.moon = 'invisible'

    const moonUp = moonhigh > moonset ?
      (now > moonhigh || now < moonset) :
      (now > moonhigh && now < moonset)

    if (moonUp) {
      if (moonphase > 45 && moonphase < 55) {
        if (mood.daytime == 'day' && cloudiness < 20 || cloudiness < 50) {
          mood.moon = 'full'
        }
      } else if (moonphase > 15 && moonphase < 85) {
        if (mood.daytime == 'day' && cloudiness < 10 || cloudiness < 20) {
          mood.moon = 'visible'
        }
      }
    }


    // Try to deduce the ambiance
    if (isNear) {
      mood.ambiance = 'unknown'
    } else if (soundLevel < -150) {
      mood.ambiance = 'quiet'
    } else if (soundLevel < -100) {
      mood.ambiance = 'moderate'
    } else if (soundLevel < 0) {
      mood.ambiance = 'loud'
    }

    if (isNear) {
      mood.luminosity = 'unknown'
    } else if (ambientLight > 16000) {
      mood.luminosity = 'radiant'
    } else if (ambientLight > 1000) {
      mood.luminosity = 'luminous'
    } else if (ambientLight > 100) {
      mood.luminosity = 'moderate'
    } else if (ambientLight > 8){
      mood.luminosity = 'soft'
    } else if (ambientLight > 1){
      mood.luminosity = 'low'
    } else {
      mood.luminosity = 'dark'
    }


    // Activity (iOS has a 0-3 confidence measure, Android is %)
    mood.activity = 'unknown'

    if (activity.confidence < 65 && activity.confidence > 3 || activity.confidence === 0) {
      mood.activity = 'unknown'
    } else if (activity.type === 'STILL' || activity.type === 'STATIONARY') {
      mood.activity = 'still'
    } else if (activity.type === 'WALKING') {
      mood.activity = 'walking'
    } else if (activity.type === 'RUNNING') {
      mood.activity = 'running'
    } else if (activity.type === 'IN_VEHICLE' || activity.type === 'AUTOMOTIVE') {
      mood.activity = 'in vehicle'
    } else if (activity.type === 'ON_BICYCLE' || activity.type === 'CYCLING') {
      mood.activity = 'cycling'
    }

    log.info('Mood: result:')
    log.info(mood)

    return mood

  },

  getTuneable(mood) {
    const { time, daytime, temperature, wind, weather, outdoor,
      moon, ambiance, luminosity, activity, action } = mood

    let params = {}

    if (daytime === 'dawn') {
      // Sunrise...
      // For some reason you're up
      // Calm songs, acoustic if the sun is here
      // No lyrics, or few
      // Have a good day <3
      if (ambiance.match(/unknown|quiet|moderate/)) {
        params.max_energy = 0.4
        params.target_energy = 0.2
        params.max_danceability = 0.3

      } else if (ambiance === 'loud') {
        params.min_energy = 0.3
        params.min_danceability = 0.6
      }

      if (time === 'morning') {
        if (weather.match(/sunny|clear|partly sunny/)) {
          params.target_valence = 0.55
          params.target_acousticness = 0.95
        }
      }

      if (outdoor === 'yes') {
        params.max_danceability = 0.8
      }

    } else if (daytime === 'day') {
      // It's daytime!
      // In the morning, unlock some dance moves
      // At noon, more discovery, high energy
      // Afternoon, lyrical songs (unless working)
      // In case of good weather, a bit more acoustic and happy
      if (time === 'morning') {
        params.max_energy = 0.7
        params.max_danceability = 0.8
        params.target_instrumentalness = 0.95
      } else if (time === 'noon' && action !== 'work') {
        params.max_energy = 0.8
        params.max_danceability = 0.9
        params.max_instrumentalness = 0.4
      } else if (time === 'afternoon' && action !== 'work') {
        params.target_speechiness = 0.5
      }

      if (time === 'morning') {
        if (weather.match(/sunny|clear|partly sunny/)) {
          params.target_valence = 0.55
          params.target_acousticness = 0.95
        }
      } else {
        if (weather.match(/sunny|clear|partly sunny/)) {
          params.target_valence = 0.65
          params.target_acousticness = 0.95
          params.min_valence = 0.15
        }
      }

      if (!outdoor.match(/no|probably not/)) {
        params.max_danceability = 0.9
      }


    } else if (daytime === 'dusk') {
      // Sun is setting, time for good melancholia
      // In case of good weather, still more acoustic and happy
      // If you're outside, less restrictions, dance!
      if (ambiance === 'quiet') {
        params.max_energy = 0.7
        params.max_danceability = 0.6

      } else if (ambiance === 'loud') {
        params.min_energy = 0.3
        params.min_danceability = 0.6
      }

      if (weather.match(/sunny|clear|partly sunny/)) {
        params.target_valence = 0.55
      }

      if (!outdoor.match(/no|probably not/)) {
        params.target_acousticness = 0.4
        params.max_danceability = 0.9
      }

    } else if (daytime === 'night') {
      // It's dark outside
      // If you live in the north and it's the winter,
      // it may still be the afternoon...
      //
      // if it's the case: rather moderate energy and valence
      // if it's late: reduce the amount of lyrical songs
      // if it's loud: probably a crowded environment, social,
      // transportations, workout or party? In that case, high up
      // danceability and energy!
      if (ambiance === 'loud') {
        params.min_danceability = 0.6
        params.min_energy = 0.6

      } else if (time === 'night time') { // late
        params.max_energy = 0.7
        params.max_valence = 0.5

      } else if (luminosity.match(/moderate|luminous|radiant/)) {
        params.min_valence = 0.3
      } 

      if (outdoor.match(/no|probably not/) &&
        ambiance.match(/unknown|quiet/) &&
        activity.match(/unknown|still|walking/)) {
        params.max_energy = 0.6
        params.max_valence = 0.5
        params.max_danceability = 0.4

        if (time === 'night time') {
          params.min_instrumentalness = 0.3
          params.target_instrumentalness = 1.0
        }
      }

    }


    // At work: mostly lyrics free music, low to medium energy
    if (action === 'work') {
      params.min_instrumentalness = 0.3
      params.max_instrumentalness = 1.0
      params.target_instrumentalness = 1.0

      if (daytime.match(/dawn|day|dusk/)) {
        params.max_energy = 0.75
        params.min_energy = 0.15
      }
    }

    log.info(`Tuneable params: deduced `)
    log.info(params)

    return params

  },
}

export default Mood;
