import React, { Component } from 'react'
import { NativeModules } from 'react-native'
import * as Location from 'expo-location'
import * as Permissions from 'expo-permissions'
import { log } from './config';
import RNSoundLevel from 'react-native-sound-level'
import ActivityRecognition from 'react-native-activity-recognition'
import NetInfo from "@react-native-community/netinfo";
import BackgroundTimer from 'react-native-background-timer';

const { SensorManager } = NativeModules

const Sensors = {

  arrayAverage(arr) {
    //Find the sum
    var sum = 0;
    for(var i in arr) {
        sum += arr[i];
    }
    //Get the length of the array
    var numbersCnt = arr.length;
    //Return the average / mean.
    return Math.round(sum / numbersCnt);
  },

  getNetInfo(callback) {
    return NetInfo.fetch().then(state => {
      callback && callback(state.type === 'wifi' && state.details.strength >= 40)
    })
  },

  async startAmbientSoundRecording() {
    let { status } = await Permissions.askAsync(Permissions.AUDIO_RECORDING)

    if (status === 'granted') {
      RNSoundLevel.start()
    }
  },

  async getLocation() {
    let { status } = await Permissions.askAsync(Permissions.LOCATION)

    if (status === 'granted') {
      return Location.getCurrentPositionAsync({
        accuracy: 6,
        maximumAge: 60000,
        timeout: 4000
      })
    } else return
  },

  async startLocationUpdates()  {
    const { status } = await Permissions.askAsync(
      Permissions.LOCATION,
      Permissions.USER_FACING_NOTIFICATIONS
    )

    if (status === 'granted') {
      await Location.startLocationUpdatesAsync(
        'background-location-check', {
          accuracy: 6,
          distanceInterval: 10,
          timeInterval: 12000,
          pausesUpdatesAutomatically: true,
        }
      )
    }
  },

  startAll() {
    ActivityRecognition.start(100)
    SensorManager.startLightSensor(200)
    SensorManager.startProximity(100)
    this.startAmbientSoundRecording()
  },

  stopAll() {
    ActivityRecognition.stop()
    SensorManager.stopLightSensor()
    SensorManager.stopProximity()
    RNSoundLevel.stop()
  },

  startTemporarily(seconds=5000) {
    BackgroundTimer.clearTimeout(this.sensorsTimer)
    this.startAll()
    this.sensorsTimer = BackgroundTimer.setTimeout(this.stopAll, seconds)
  },
}

export default Sensors;
